// Manages rendering SessionManagerFields in lieu of React support in ModelAdmin
import React from 'react';
import ReactDOM from 'react-dom';
import { loadComponent } from 'lib/Injector'; // eslint-disable-line

const FIELD_SELECTOR = '.js-injector-boot [data-field-type="session-manager-field"]';

window.jQuery.entwine('ss', ($) => {
  $(FIELD_SELECTOR).entwine({
    onmatch() {
      const SessionManagerField = loadComponent('SessionManagerField');
      const { readOnly /* , schema: {
        backupMethod,
        defaultMethod,
        registeredMethods,
        availableMethods,
        allAvailableMethods,
        resources,
        endpoints,
        backupCreatedDate,
        resetEndpoint,
        isMFARequired,
      } */ } = this.data('schema');

      // ReactDOM.render(
      //     <div>test</div>,
      //   this[0]
      // );
      ReactDOM.render(
        <SessionManagerField
          // backupMethod={backupMethod}
          // readOnly={readOnly}
          // initialDefaultMethod={defaultMethod}
          // initialRegisteredMethods={registeredMethods}
          // initialAvailableMethods={availableMethods}
          // allAvailableMethods={allAvailableMethods}
          // resources={resources}
          // endpoints={endpoints}
          // backupCreatedDate={backupCreatedDate}
          // resetEndpoint={resetEndpoint}
          // isMFARequired={isMFARequired}
        />,
        this[0]
      );
    }
  });
});



